.register {
	position: relative;
	cursor: pointer;
}
.register_action {
	width: 5rem;
	height: 5rem;
	margin: auto;
	border-radius: 50%;
	overflow: hidden;
	font-size: 3em;
	display: flex;
	justify-content: center;
	align-items: center;
	background: var(--text_color_primary);
	color: var(--background_color);
}
.register_title {
	margin-top: 1rem;
}



